export const toArrayWithKey = (obj: any, keyAs: any) => {
  const result = [];
  for (const key in obj) {
    result.push({
      [keyAs]: key,
      ...obj[key],
    });
  }
  return result;
};

export enum EStopsFilterType {
  ANY,
  NON_STOP,
  ONE_STOP,
  TWO_STOP,
}

const fetchSearchResults = (searchId: string, signal?: any) => {
  return fetchWithRetry(fetch)(
    `https://apiv2.flightscanner.com/flights-search-results?uuid=${searchId}`,
    {
      signal,
      ...retryConfig,
    }
  )
    .then((r) => r.json())
    .catch((error) => {
      if (error?.name !== "AbortError") {
        captureException(error);
      }
    });
};

