const fetchSearchResultsDuffel = (query: any, signal: any) => {
  const passengers = [] as any;

  passengers.push(
    ...Array.from({ length: Number(query.adults || 1) }, (_, i) => ({
      type: "adult",
    }))
  );

  passengers.push(
    ...Array.from({ length: Number(query.children || 0) }, (_, i) => ({
      age: 2,
    }))
  );

  passengers.push(
    ...Array.from({ length: Number(query.infants || 0) }, (_, i) => ({
      age: 2,
    }))
  );

  const slices = [
    {
      origin: query.from?.split(":")[0],
      destination: query.to?.split(":")[0],
      departure_date: new Date(query.departureDate).toISOString().split("T")[0],
    },
  ];

  if (query.returnDate && query.returnDate !== "none") {
    slices.push({
      origin: query.to?.split(":")[0],
      destination: query.from?.split(":")[0],
      departure_date: new Date(query.returnDate).toISOString().split("T")[0],
    });
  }

  return fetchWithRetry(fetch)("/api/search", {
    method: "post",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      slices,
      passengers,
    }),
    signal,
  }).then((r) => r.json()).catch((error) => {
    if (error?.name !== "AbortError") {
      captureException(error);
    }
  });
};

const fetchSearchResultsKiwi = (query: any, currency: string, signal: any) => {
  const params = {
    fly_from: query.from?.split(":")[0],
    fly_to: query.to?.split(":")[0],
    date_from: new Date(query.departureDate).toISOString().split("T")[0],
    date_to: new Date(query.departureDate).toISOString().split("T")[0],
    adults: Number(query.adults || 1),
    children: Number(query.children || 0),
    infants: Number(query.infants || 0),
    vehicle_type: "aircraft",
    curr: currency,
  } as any;

  if (query.tripClass === ETripClass.business) {
    params.selected_cabins = "C";
    params.mix_with_cabins = "W";
  }

  if (query.returnDate && query.returnDate !== "none") {
    params.return_from = new Date(query.returnDate).toISOString().split("T")[0];
    params.return_to = new Date(query.returnDate).toISOString().split("T")[0];
  }
  return fetchWithRetry(fetch)(
    `${API_URL}/v2/search?` +
    Object.keys(params)
      .map((k) => k + "=" + params[k])
      .join("&"),
    {
      headers: {
        apiKey: API_KEY,
      },
      signal,
      ...retryConfig,
    }
  )
    .then((r) => r.json())
    .catch((error) => {
      if (error?.name !== "AbortError") {
        captureException(error);
      }
    });
};

