export const firestoreAutoId = (): string => {
  const CHARS = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'

  let autoId = ''

  for (let i = 0; i < 20; i++) {
    autoId += CHARS.charAt(
      Math.floor(Math.random() * CHARS.length)
    )
  }
  return autoId
}

const flightSearch = (
  flightSearchRequest: any,
  locale: string,
  currencyCode: string,
  gclid?: string
) => {
  const subId = firestoreAutoId();
  const flightSearchRequestWithData = {
    marker: `${marker}.${subId}`,
    host: "www.flightscanner.com",
    user_ip: "127.0.0.1", // TODO do we have user ip on front-end? OR we need to modify end-point to generate signature for us.
    locale,
    trip_class: flightSearchRequest.trip_class,
    passengers: flightSearchRequest.passengers,
    segments: flightSearchRequest.segments,
  } as any;

  if (currencyCode) {
    flightSearchRequestWithData.currency = currencyCode;
  }

  const signature = generateSignature(
    JSON.parse(JSON.stringify(flightSearchRequestWithData)),
    "ff9339822cba2aeabedc70c12c502728"
  );

  const body = {
    ...flightSearchRequestWithData,
    signature,
  };

  return {
    url: "https://apiv2.flightscanner.com/flights-search",
    body,
    subId,
    gclid
  };
};

const generateFlightUniqueId = (
  airportFrom: string,
  airportTo: string,
  operating_carrier: string,
  flightNumber: string
) => {
  return `${airportFrom.toUpperCase()}_${airportTo.toUpperCase()}_${operating_carrier}_${flightNumber}`;
};

const generateTicketId = (flightUniqueIds: string[]) => {
  return flightUniqueIds.join("|");
};

const setFlightSortInfo = (sortedFlights: ISortedFlights) => {
  const bestFlight = sortedFlights[EFlightSort.BEST][0];
  const cheapFlight = sortedFlights[EFlightSort.CHEAP][0];
  const quickFlight = sortedFlights[EFlightSort.QUICK][0];

  return {
    best: {
      amount:
        (bestFlight && sortFlightTerms(bestFlight.terms)[0].unified_price) || 0,
      duration: (bestFlight && bestFlight.total_duration) || 0,
    },
    cheap: {
      amount:
        (cheapFlight && sortFlightTerms(cheapFlight.terms)[0].unified_price) ||
        0,
      duration: (cheapFlight && cheapFlight.total_duration) || 0,
    },
    quick: {
      amount:
        (quickFlight && sortFlightTerms(quickFlight.terms)[0].unified_price) ||
        0,
      duration: (quickFlight && quickFlight.total_duration) || 0,
    },
  };
};

