type initialStateType = {
  airports: any[];
  airlines: any[];
  gates: any[];
  searchId: string | undefined;
  searchComplete: boolean;
  limit: number;
  rates: any;
  filteredFlightsCount: number;
  sortedFlightsInfo: any;
  filtersBoundaries: any;
  flightSortBy: EFlightSort.BEST;
  flightsFilters: IFlightsFilters;
  proposals: any[];
  allProposalsLength: number;
  gclid: string | undefined;
  subId: string | undefined;
};

const initialState: initialStateType = {
  airports: [],
  airlines: [],
  gates: [],
  searchId: undefined,
  searchComplete: false,
  limit: 10,
  rates: undefined,
  filteredFlightsCount: 0,
  sortedFlightsInfo: undefined,
  filtersBoundaries: { ...initialFiltersBnd },
  flightSortBy: EFlightSort.BEST,
  flightsFilters: {
    stop: EStopsFilterType.ANY,
    duration: [0, 0],
    price: [0, 0],
    times: [],
    airlines: [],
    gates: [],
  },
  proposals: [],
  allProposalsLength: 0,
  subId: undefined,
  gclid: undefined,
};

function reducer(state: initialStateType, action: any) {
  switch (action.type) {
    case "setVisibleProposals":
      return {
        ...state,
        proposals: action.proposals,
        filteredFlightsCount: action.filteredFlights.length,
        sortedFlightsInfo: action.sortedFlightsInfo,
        airports: action.airports
          ? { ...merge(state.airports, action.airports) }
          : state.airports,
        gates: action.gates
          ? { ...merge(state.gates, action.gates) }
          : state.gates,
        airlines: action.airlines
          ? { ...merge(state.airlines, action.airlines) }
          : state.airlines,
        flightSortBy: action.sortBy,
        flightsFilters: action.flightsFilters
          ? action.flightsFilters
          : state.flightsFilters,
        allProposalsLength: action.allProposalsLength,
      };
    case "setSearchId":
      return {
        ...state,
        searchId: action.searchId,
        rates: action.rates,
        subId: action.subId,
        gclid: action.gclid,
      };

    case "searchComplete":
      return {
        ...state,
        searchComplete: true,
      };
    case "resetSearch":
      return {
        ...state,
        ...initialState,
      };
    case "loadMore":
      return {
        ...state,
        limit: state.limit + 10,
      };
    case "setFiltersBoundaries":
      return {
        ...state,
        filtersBoundaries: action.filtersBoundaries,
      };
    default:
      throw new Error();
  }
}

const DurationSlider = ({ state, onDurationChange = () => { } }: any) => {
  const [_durationV, setDurationV] = useState([]);

  useEffect(() => {
    if (
      _durationV &&
      state.flightsFilters.duration[0] === 0 &&
      state.flightsFilters.duration[1] === 0
    ) {
      setDurationV([]);
    }
  }, [state.flightsFilters.duration]);

  return (
    <Slider
      getAriaLabel={() => "Duration range"}
      min={state.filtersBoundaries.duration[0]}
      max={state.filtersBoundaries.duration[1]}
      valueLabelFormat={(v) => secondsToTime(v * 60)}
      marks={[
        {
          value: state.filtersBoundaries.duration[0],
          label: secondsToTime(state.filtersBoundaries.duration[0] * 60),
        },
        {
          value: state.filtersBoundaries.duration[1],
          label: secondsToTime(state.filtersBoundaries.duration[1] * 60),
        },
      ]}
      value={[
        _durationV[0] || state.flightsFilters.duration[0],
        _durationV[1] ||
        state.flightsFilters.duration[0] ||
        state.filtersBoundaries.duration[1],
      ]}
      onChange={(v: any) => {
        setDurationV(v.target.value);
      }}
      onChangeCommitted={(v) => {
        onDurationChange(_durationV);
      }}
      valueLabelDisplay="auto"
      getAriaValueText={(v) => {
        return `${v}c`;
      }}
    />
  );
};

const PriceSlider = ({ state, onPriceChange = () => { }, currency }: any) => {
  const intl = useIntl();
  const [_priceV, setPriceV] = useState([]);

  useEffect(() => {
    if (
      _priceV &&
      state.flightsFilters.price[0] === 0 &&
      state.flightsFilters.price[1] === 0
    ) {
      setPriceV([]);
    }
  }, [state.flightsFilters.price]);

  const formartPrice = (price: number) => {
    return intl.formatNumber(price / state.rates[currency.code.toLowerCase()], {
      style: "currency",
      currency: currency.code,
      maximumFractionDigits: 0,
      minimumFractionDigits: 0,
    });
  };

  if (!currency || !state?.rates) {
    return null;
  }

  return (
    <Slider
      getAriaLabel={() => "Price range"}
      min={state.filtersBoundaries.price[0]}
      max={state.filtersBoundaries.price[1]}
      valueLabelFormat={(v) => formartPrice(v)}
      marks={[
        {
          value: state.filtersBoundaries.price[0],
          label: formartPrice(state.filtersBoundaries.price[0]),
        },
        {
          value: state.filtersBoundaries.price[1],
          label: formartPrice(state.filtersBoundaries.price[1]),
        },
      ]}
      value={[
        _priceV[0] || state.flightsFilters.price[0],
        _priceV[1] ||
        state.flightsFilters.price[0] ||
        state.filtersBoundaries.price[1],
      ]}
      onChange={(v: any) => {
        setPriceV(v.target.value);
      }}
      onChangeCommitted={(v) => {
        onPriceChange(_priceV);
      }}
      valueLabelDisplay="auto"
      getAriaValueText={(v) => {
        return `${v}c`;
      }}
    />
  );
};

