const Home: NextPage = () => {
  const intl = useIntl();
  const t = useTranslations();
  const theme = useTheme();
  const { query, locale }: any = useRouter();
  const { currency } = useGlobal();
  const [state, dispatch] = useReducer(reducer, initialState);

  const canResetRef = useRef<boolean>();
  const cachedKiwiResponse = useRef();
  const cachedDuffelResponse = useRef();
  const _searchRequestPollTimeout = useRef<NodeJS.Timeout>();
  const searchResults = useRef<any>([]);
  const _proposalIndexMap = useRef<any>({});
  const _allProposals = useRef<any>([]);
  const _filtersBoundaries = useRef<any>([]);
  const _controller = useRef(
    typeof window !== "undefined" && new AbortController()
  );
  const _controllerKiwi = useRef(
    typeof window !== "undefined" && new AbortController()
  );
  const _controllerDuffel = useRef(
    typeof window !== "undefined" && new AbortController()
  );
  const _signal = useRef(_controller.current && _controller.current.signal);
  const _signalKiwi = useRef(
    _controllerKiwi.current && _controllerKiwi.current.signal
  );
  const _signalDuffel = useRef(
    _controllerDuffel.current && _controllerDuffel.current.signal
  );

  // Used to avoid waiting for state to update state will pesist to final correct state but while we have async request we need this
  const sortByRef = useRef<EFlightSort>(initialState.flightSortBy);
  const flightsFiltersRef = useRef({ ...initialState.flightsFilters });

  const fetchKiwiFlights = (query: any, currencyCode: string) => {
    fetchSearchResultsKiwi(query, currencyCode, _signalKiwi.current).then(
      (res: any) => {
        cachedKiwiResponse.current = res;
      }
    ).catch((error) => {
      if (error?.name !== "AbortError") {
        captureException(error);
      }
    });
  };

  const fetchDuffelFlights = async (query: any) => {
    fetchSearchResultsDuffel(query, _signalDuffel.current).then((res: any) => {
      cachedDuffelResponse.current = res;
    }).catch((error) => {
      if (error?.name !== "AbortError") {
        captureException(error);
      }
    });
  };

  const onSortingChange = (sortBy: EFlightSort) => {
    sortByRef.current = sortBy;
    setVisibleProposals({
      sortBy,
    });
    trackEvent({
      action: "onSortingChange",
      label: String(sortBy),
    });
  };

  const setVisibleProposals = useCallback(
    ({ gates = {}, airlines = {}, airports = {} }: any = {}) => {
      // Can change this during flights in progress so it work any time
      const sortBy = sortByRef.current;
      const flightsFilters = flightsFiltersRef.current;

      // Filters
      const filteredFlights = filterFlights(
        _allProposals.current,
        flightsFilters
      );

      // Sorting
      const sortedFlights = sortFlights(filteredFlights);
      const sortedFlightsInfo = setFlightSortInfo(sortedFlights);

      // Set + Pagination
      const proposals = sortedFlights[sortBy];

      dispatch({
        type: "setVisibleProposals",
        proposals,
        filteredFlights,
        sortedFlightsInfo,
        gates, // will be added to global state
        airlines, // ^
        airports, // ^
        sortBy,
        flightsFilters,
        allProposalsLength: _allProposals.current.length,
      });
    },
    []
  );

  const onDuffelFlightsResponse = (res: any) => {
    const gates_info = {
      1337: {
        currency_code: "usd",
        is_airline: false,
        label: "Flightscanner",
      },
    };

    res.data.offers.forEach((ticket: any) => {
      const flightScannerIdCombined: any[] = [];

      const currRate = state.rates[ticket.total_currency.toLowerCase()];
      const priceInRubles =
        currRate > 1
          ? ticket.total_amount * currRate
          : ticket.currRate / currRate;

      const terms = {
        1337: {
          currency: "rub", //currency.code,
          agent_id: 1337,
          // price: ticket.base_amount,
          price: priceInRubles,
          unified_price: priceInRubles,
          // unified_price:
          //   (state.rates &&
          //     priceInRubles * state.rates[currency.code.toLowerCase()]) ||
          //   0,
          url: ticket.id,
        },
      };

      ticket.slices.forEach((slice: any) => {
        slice.segments.forEach((segment: any) => {
          const airlineCode =
            segment.marketing_carrier.iata_code ||
            segment.operating_carrier.iata_code;
          const uniqueId = generateFlightUniqueId(
            segment.origin.iata_code,
            segment.destination.iata_code,
            airlineCode,
            segment.marketing_carrier_flight_number
          );
          flightScannerIdCombined.push(uniqueId);
        });
      });

      const finalFlightScannerId = generateTicketId(
        flightScannerIdCombined
      ) as any;

      const index = _proposalIndexMap.current[finalFlightScannerId];
      _allProposals.current[index] = {
        ...merge(_allProposals.current[index], { terms: terms }),
      };
    });

    setVisibleProposals({
      gates: gates_info,
    });
  };

  const onKiwiFlightsResponse = useCallback(
    (response: any) => {
      if (response?.data) {
        const gates_info = {
          203: {
            average_rate: 5,
            currency_code: "usd",
            email: "support.ru@kiwi.com",
            helplink: "https://www.kiwi.com/ru/helpcenter",
            is_airline: false,
            is_exclusive: "true",
            label: "Kiwi.com",
            mobile_version: "true",
            payment_methods: ["card"],
            phone: ["+7 (499) 348 80 53"],
            productivity: 0,
            rates: 2967,
            show_feedback_popup: true,
            site: "https://www.kiwi.com/ru/",
            working_hours: "",
            is_kiwi: true,
          },
        };

        response.data.forEach((ticket: any) => {
          const flightScannerIdCombined: any[] = [];
          const deepLink = ticket.deep_link;

          const terms = {
            203: {
              currency: "rub",
              // flights_baggage: any;
              // flights_handbags: any;
              agent_id: 203,
              price: ticket.price,
              unified_price: ticket.price,
              url: deepLink,
            },
          };

          ticket.route.forEach((r: any) => {
            const airlineCode = r.airline.length
              ? r.airline
              : r.operating_carrier;
            const uniqueId = generateFlightUniqueId(
              r.flyFrom,
              r.flyTo,
              airlineCode,
              r.flight_no
            );
            flightScannerIdCombined.push(uniqueId);
          });

          const finalFlightScannerId = generateTicketId(
            flightScannerIdCombined
          ) as any;
          const index = _proposalIndexMap.current[finalFlightScannerId];
          _allProposals.current[index] = {
            ...merge(_allProposals.current[index], { terms: terms }),
          };
        });

        setVisibleProposals({
          gates: gates_info,
        });
      }
    },
    [setVisibleProposals, currency.code, state.rates]
  );

  const onFlightsResultsProgress = useCallback(
    (items: any) => {
      Array.prototype.push.apply(searchResults.current, items);

      // Update total results count
      // this.resultsLength = this.searchResults.length;

      // TEMP save new gates/airports/airlines
      let airports = {};
      let airlines = {};
      let gates = {};

      // Merge results
      searchResults.current.forEach((gate: any) => {
        airports = { ...merge(airports, gate.airports) };
        // this.airlines = { ...merge(this.airlines, gate.airlines) };
        airlines = { ...merge(airlines, gate.airlines) };
        // JSON.parse(JSON.stringify())
        // TODO dont overwrite kiwi if already exists
        // this.gates = { ...merge(this.gates, gate.gates_info) };
        gates = { ...merge(gates, gate.gates_info) };

        if (gate.filters_boundary) {
          _filtersBoundaries.current.push(gate.filters_boundary);
          setFiltersBoundaries();
        }
        // Save and update existing proposals
        // PROPOSAL = FULL TICKET WITH ROUTES
        gate.proposals.forEach((proposal: any) => {
          // generate our own unique id
          // if doesnt exists lets push it as a new ticket
          // TODO change to object instead
          proposal.flightScannerId = [];
          // SEGMENT =    E WAY or RETURN
          proposal.segment.map((segment: any) => {
            // Each segment has one or more flights
            // Assign unique flight id of flightscanner
            // and to the whole ticket combine all of them
            return segment.flight.map((segmentFlight: any) => {
              const uniqueId = generateFlightUniqueId(
                segmentFlight.departure,
                segmentFlight.arrival,
                segmentFlight.marketing_carrier ||
                segmentFlight.operating_carrier,
                segmentFlight.number
              );
              segmentFlight.flightScannerId = uniqueId;
              proposal.flightScannerId.push(uniqueId);
              return segmentFlight;
            });
          });

          proposal.flightScannerId = generateTicketId(proposal.flightScannerId);

          // FlightNetwork, SmartFares, Gotogate, SuperSaverunified_price
          // const blackList = ["112", "132", "362", "108"];

          // const hasWhiteTerms = Object.keys(proposal.terms).length > 0;

          if (!_proposalIndexMap.current[proposal.flightScannerId]) {
            // if (hasWhiteTerms) {
            const index = _allProposals.current.push({
              ...proposal,
              // terms: termsWithoutBackList,
              terms: proposal.terms, // if only seller of the ticket show it
            });
            _proposalIndexMap.current[proposal.flightScannerId] = index - 1;
            // }
          } else {
            const index = _proposalIndexMap.current[proposal.flightScannerId];

            const blackList = ["112", "132", "362", "108"];
            const termsWithoutBackList = { ...proposal.terms };

            blackList.forEach((black) => {
              delete termsWithoutBackList[black];
            });

            if (Object.keys(termsWithoutBackList).length > 0) {
              // filter previous ticket maybe we have blacklist there
              const termsFilttered = {
                ..._allProposals.current[index].terms,
              };

              blackList.forEach((black) => {
                delete termsFilttered[black];
              });

              _allProposals.current[index].terms = { ...termsFilttered };

              _allProposals.current[index] = {
                ...merge(_allProposals.current[index], {
                  terms: termsWithoutBackList,
                }),
              };
            }
          }
        });
      });

      // TODO this step is where we set visible filters & apply current filters to?
      // TODO What happens when a filter is changed during search? and after completed...
      setVisibleProposals({
        gates,
        airlines,
        airports,
      });
    },
    [setVisibleProposals]
  );

  const cancelPreviousApiCalls = () => {
    _searchRequestPollTimeout.current &&
      clearTimeout(_searchRequestPollTimeout.current);
  };

  const onFlightsResultsComplete = useCallback(() => {
    if (cachedKiwiResponse.current) {
      onKiwiFlightsResponse(cachedKiwiResponse.current);
    }
    if (cachedDuffelResponse.current) {
      onDuffelFlightsResponse(cachedDuffelResponse.current);
    }
    dispatch({ type: "searchComplete" });
  }, [onKiwiFlightsResponse]);

  const processFlightsSearchResponse = useCallback(
    (response: any, searchId: string) => {
      if (response?.length) {
        const lastItem = response && response[response.length - 1];
        const isFinalArray = Object.keys(lastItem).length === 1;
        onFlightsResultsProgress(
          isFinalArray ? response.slice(0, response.length - 1) : response
        );

        if (isFinalArray) {
          onFlightsResultsComplete();
        } else {
          _searchRequestPollTimeout.current = setTimeout(() => {
            cancelPreviousApiCalls();
            fetchSearchResults(searchId, _signal.current).then((response) => {
              processFlightsSearchResponse(response, searchId);
            }).catch((error) => {
              if (error?.name !== "AbortError") {
                captureException(error);
              }
            });
          }, pollEverySeconds);
        }
      }
    },
    [onFlightsResultsComplete, onFlightsResultsProgress]
  );

  const resetSearchState = () => {
    dispatch({ type: "resetSearch" });
    _allProposals.current = [];
    _proposalIndexMap.current = {};
    searchResults.current = [];
    _filtersBoundaries.current = [];
    cachedKiwiResponse.current = undefined;
    canResetRef.current = false;
    cancelPreviousApiCalls();
  };

  const handleLoadMore = () => {
    dispatch({ type: "loadMore" });
    trackEvent({
      action: "handleLoadMore",
    });
  };

  useEffect(() => {
    const controller = new AbortController();
    const signal = controller.signal;

    if (query?.tripClass === ETripClass.first) {
      fetchDuffelFlights(query);
    } else {
      if (query?.from && !state.searchId && !canResetRef.current) {
        // we have no searchId and no reset start fetching
        const flightSearchRequest = {
          trip_class: query.tripClass || ETripClass.economy,
          passengers: {
            adults: Number(query.adults || 1),
            children: Number(query.children || 0),
            infants: Number(query.infants || 0),
          },
          segments: [
            {
              origin: query.from?.split(":")[0],
              destination: query.to?.split(":")[0],
              date: new Date(query.departureDate).toISOString().split("T")[0],
            },
          ],
        } as any;

        if (query.returnDate && query.returnDate !== "none") {
          flightSearchRequest.segments.push({
            origin: query.to?.split(":")[0],
            destination: query.from?.split(":")[0],
            date: new Date(query.returnDate).toISOString().split("T")[0],
          });
        }

        const { url, body, subId, gclid } = flightSearch(
          flightSearchRequest,
          locale,
          currency.code,
          query.gclid
        );

        fetchWithRetry(fetch)(url, {
          body: JSON.stringify(body),
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
          signal,
          ...retryConfig,
        })
          .then((r) => r.json())
          .then((r) => {
            dispatch({
              type: "setSearchId",
              searchId: r.search_id,
              rates: r.currency_rates,
              subId,
              gclid,
            });
          })
          .catch((error) => {
            if (error?.name !== "AbortError") {
              captureException(error);
            }
          });

        fetchKiwiFlights(query, "rub");

        // fetchDuffelFlights(query);

        return () => {
          controller?.abort();
        };
      } else if (query?.from && state.searchId && !canResetRef.current) {
        // we got the search id next change of search id we need to reset the searchId
        canResetRef.current = true; // first call is always not the one to reset
      } else if (query?.from && state.searchId && canResetRef.current) {
        // we got locale, query, currency change so reset state (searchId will change and we will fetch new searchId)
        controller?.abort();
        resetSearchState();
      }
    }
  }, [query, locale, currency, state.searchId]);

  useEffect(() => {
    if (state.searchId) {
      // TODO after reset state is done only start fetching calls
      // resetSearchState();
      fetchSearchResults(state.searchId, _signal.current).then((response) => {
        processFlightsSearchResponse(response, state.searchId);
      }).catch((error) => {
        if (error?.name !== "AbortError") {
          captureException(error);
        }
      });

      () => {
        cancelPreviousApiCalls();
      };
    }
  }, [state.searchId, processFlightsSearchResponse]);

  const handleFilter = (filterName: string, filterValue: string) => {
    flightsFiltersRef.current = {
      ...state.flightsFilters,
      [filterName]: filterValue,
    };
    setVisibleProposals();
    trackEvent({
      action: "handleFilter",
      category: filterName,
      label: String(filterValue),
    });
  };

  const setFiltersBoundaries = () => {
    const maxPrice = Math.ceil(
      maxBy(_filtersBoundaries.current, (o: any) => o.price.max).price.max * 1.1
    );
    const minPrice = Math.floor(
      minBy(_filtersBoundaries.current, (o: any) => o.price.min).price.min
    );
    const maxDuration = Math.ceil(
      maxBy(_filtersBoundaries.current, (o: any) => o.flights_duration.max)
        .flights_duration.max * 1.1
    );
    const minDuration = Math.floor(
      minBy(_filtersBoundaries.current, (o: any) => o.flights_duration.min)
        .flights_duration.min
    );
    const airlines = toArrayWithKey(state.airlines, "_code");
    const gates = toArrayWithKey(state.gates, "_code");

    dispatch({
      type: "setFiltersBoundaries",
      filtersBoundaries: {
        ...state.filtersBoundaries,
        price: [minPrice, maxPrice],
        duration: [minDuration, maxDuration],
        times: [],
        airlines,
        gates,
      },
    });
  };

  const resetFilters = () => {
    flightsFiltersRef.current = { ...initialState.flightsFilters };
    setVisibleProposals();
    trackEvent({
      action: "resetFilters",
    });
  };

  return (
    <>
      <Box
        sx={{
          pb: 2,
          backgroundColor:
            theme.palette.mode === "light" ? "#F3F6F9" : "inherit",
        }}
      >
        <Head>
          <title>{t("seo-search-page-title")}</title>
        </Head>
        <ResponsiveAppBar />
        <Paper
          variant={theme.palette.mode === "light" ? "outlined" : "elevation"}
          sx={{ pb: 2, borderLeft: "none", borderRight: "none" }}
          square
        >
          <SearchForm maxWidth="lg" showBooking={false} rates={state.rates} />
        </Paper>

        <Container maxWidth="lg" sx={{ py: 2 }}>
          <Box sx={{ display: "flex" }}>
            <Box
              sx={{
                width: "268px",
                display: { xs: "none", md: "block" },
                mr: 2,
              }}
            >
              {state.allProposalsLength > 0 ? (
                <Box>
                  <Typography gutterBottom>
                    {t("filters-flights-count", {
                      value: state.filteredFlightsCount,
                      value2: _allProposals.current.length,
                    })}
                  </Typography>
                  <Filters
                    state={state}
                    handleFilter={handleFilter}
                    currency={currency}
                    resetFilters={resetFilters}
                  />
                </Box>
              ) : (
                <Stack spacing={2}>
                  <Skeleton variant="rectangular" width={"100%"} height={160} />
                  <Skeleton variant="rectangular" width={"100%"} height={160} />
                  <Skeleton variant="rectangular" width={"100%"} height={160} />
                </Stack>
              )}
            </Box>
            <Box sx={{ flex: 1 }}>
              {/* <p>Monthly overview of the prices or flight schedule</p> */}

              <MobileFilters
                state={state}
                _allProposals={_allProposals}
                handleFilter={handleFilter}
                resetFilters={resetFilters}
                currency={currency}
              />

              {!state.searchComplete && (
                <Paper sx={{ p: 2, mb: 2 }} elevation={2}>
                  <Box
                    sx={{ display: "flex", justifyContent: "space-between" }}
                  >
                    <Typography sx={{ mb: 1 }}>
                      {t("search-for-flights")}
                    </Typography>
                    {_allProposals.current.length > 1 && (
                      <Typography>
                        {_allProposals.current.length} {t("flights")}
                      </Typography>
                    )}
                  </Box>
                  <LinearProgress />
                </Paper>
              )}

              {state.allProposalsLength > 0 && state.sortedFlightsInfo ? (
                <Paper>
                  <Tabs
                    value={state.flightSortBy}
                    onChange={(e, tV) => onSortingChange(tV)}
                    // centered
                    textColor="inherit"
                    // indicatorColor="secondary"
                    variant="fullWidth"
                    sx={{ mb: 2 }}
                  >
                    <Tab
                      label={
                        <Box sx={{ alignItems: "start" }}>
                          <Typography>
                            {t("flights-sorting-tabs-best")}
                          </Typography>
                          <Typography>
                            {state.rates &&
                              currency &&
                              intl.formatNumber(
                                state.sortedFlightsInfo.best.amount /
                                state.rates[currency.code.toLowerCase()],
                                {
                                  style: "currency",
                                  currency: currency.code,
                                  maximumFractionDigits: 0,
                                  minimumFractionDigits: 0,
                                }
                              )}{" "}
                            {" "}
                            {secondsToTime(
                              state.sortedFlightsInfo?.best.duration * 60
                            )}
                          </Typography>
                        </Box>
                      }
                    />
                    <Tab
                      label={
                        <Box>
                          <Typography>
                            {t("flights-sorting-tabs-cheap")}
                          </Typography>
                          <Typography>
                            {state.rates &&
                              currency &&
                              intl.formatNumber(
                                state.sortedFlightsInfo.cheap.amount /
                                state.rates[currency.code.toLowerCase()],
                                {
                                  style: "currency",
                                  currency: currency.code,
                                  maximumFractionDigits: 0,
                                  minimumFractionDigits: 0,
                                }
                              )}{" "}
                            {" "}
                            {secondsToTime(
                              state.sortedFlightsInfo?.cheap.duration * 60
                            )}
                          </Typography>
                        </Box>
                      }
                    />
                    <Tab
                      label={
                        <Box>
                          <Typography>
                            {t("flights-sorting-tabs-quick")}
                          </Typography>
                          <Typography>
                            {state.rates &&
                              currency &&
                              intl.formatNumber(
                                state.sortedFlightsInfo.quick.amount /
                                state.rates[currency.code.toLowerCase()],
                                {
                                  style: "currency",
                                  currency: currency.code,
                                  maximumFractionDigits: 0,
                                  minimumFractionDigits: 0,
                                }
                              )}{" "}
                            {" "}
                            {secondsToTime(
                              state.sortedFlightsInfo?.quick.duration * 60
                            )}
                          </Typography>
                        </Box>
                      }
                    />
                  </Tabs>
                </Paper>
              ) : (
                <Stack spacing={2} direction="row" sx={{ mb: 2 }}>
                  <Skeleton
                    variant="rectangular"
                    sx={{ flex: 1 }}
                    height={90}
                  />
                  <Skeleton
                    variant="rectangular"
                    sx={{ flex: 1 }}
                    height={90}
                  />
                  <Skeleton
                    variant="rectangular"
                    sx={{ flex: 1 }}
                    height={90}
                  />
                </Stack>
              )}

              {state.allProposalsLength > 0 ? (
                <>
                  <FlightTickets
                    proposals={state.proposals}
                    limit={state.limit}
                    globalAirlines={state.airlines}
                    globalAirports={state.airports}
                    globalGates={state.gates}
                    rates={state.rates}
                    searchId={state.searchId}
                    subId={state.subId}
                    gclid={state.gclid}
                  />
                  {state.proposals.length === 0 && (
                    <Paper sx={{ p: 2, textAlign: "center" }}>
                      <Typography>
                        {t("search-results-reset-filter")}
                      </Typography>
                      <Button
                        variant="contained"
                        disableElevation
                        sx={{ mt: 2, width: "100%" }}
                        onClick={resetFilters}
                      >
                        {t("search-results-reset-filter-btn")}
                      </Button>
                    </Paper>
                  )}
                </>
              ) : (
                <Stack spacing={2}>
                  <Skeleton variant="rectangular" height={160} />
                  <Skeleton variant="rectangular" height={160} />
                  <Skeleton variant="rectangular" height={160} />
                </Stack>
              )}
              {state.proposals?.length > state.limit && (
                <Button
                  variant="contained"
                  disableElevation
                  sx={{ mt: 2, width: "100%" }}
                  onClick={handleLoadMore}
                >
                  {t("load-more")}
                </Button>
              )}
            </Box>
          </Box>
        </Container>
      </Box>
      <Footer />
    </>
  );
};

export async function getStaticProps({ locale }: GetStaticPropsContext) {
  return {
    props: {
      // You can get the messages from anywhere you like. The recommended
      // pattern is to put them in JSON files separated by language and read
      // the desired one based on the `locale` received from Next.js.
      messages: (await import(`../messages/index/${locale || 'en'}.json`)).default,
    },
  };
}

export default Home;
